<template>
  <el-dialog
    width="75%"
    title=""
    :visible="visible"
    :before-close="() => $emit('update:visible', false)"
  >
    <ol>
      <li><a href="static/lucy.js" download="lucy.js">lucy.js</a></li>
      <li>
        lucy.js(vueelement-ui)
        <code><pre>{{importCode}}</pre></code>
      </li>
      <li>
        jsondata-sourcelucy
        <code><pre>{{viewerCode}}</pre></code>
      </li>
      <li><a href="/lucy/test" target="_blank"></a></li>
    </ol>
    <span slot="footer">
      <el-button @click="$emit('update:visible', false)"></el-button>
    </span>
  </el-dialog>
</template>

<script>
import Code from '@/components/Code';
export default {
  props: ['dataSource', 'formConfig', 'visible'],
  components: {
    Code
  },
  data() {
    return {
      formData: {},
      // eslint-disable-next-line no-useless-escape
      importCode: '<script src="/lucy.js"><\/script>',
      viewerCode: `<lucy\n  :data-source="json"\n  v-model="value"\n/>`,
    };
  }
};
</script>

<style lang="scss" scoped>

</style>



